@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

body {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgb(125, 150, 195);
  background: linear-gradient(
    90deg,
    rgba(125, 150, 195, 1) 35%,
    rgba(193, 207, 232, 1) 100%
  );
}

.Wrapper {
  width: 340px;
  height: 540px;
  padding: 10px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  background: rgb(0, 24, 69);
  background: linear-gradient(
    90deg,
    rgba(0, 24, 69, 1) 35%,
    rgba(8, 33, 80, 1) 100%
  );
}

.Screen {
  height: 100px;
  width: 100%;
  margin-bottom: 10px;
  padding: 0 10px;
  background: rgb(12, 60, 91);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  color: white;
  font-weight: bold;
  box-sizing: border-box;
}

.buttonBox {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-gap: 10px;
}

button {
  border: none;
  background-color: rgb(80, 60, 209);
  font-size: 24px;
  color: rgb(255, 255, 255);
  font-weight: bold;
  cursor: pointer;
  border-radius: 10px;
  outline: none;
}

button:hover {
  background-color: rgb(61, 43, 184);
}

.equals {
  grid-column: 3 / 5;
  background-color: rgb(243, 61, 29);
}

.equals:hover {
  background-color: rgb(228, 39, 15);
}

.operator {
  background-color: rgb(98, 182, 203);
}
.operator:hover {
  background-color: rgb(65, 154, 177);
}

